<template>
  <div class="btn-group" :class="{open: show}">
    <button ref="btn" type="button" class="btn btn-default dropdown-toggle" @click="toggleDropdown" @blur="show = (search ? show : false)" :disabled="disabled">
      <span class="btn-placeholder" v-show="showPlaceholder">{{placeholder}}</span>
      <span class="btn-content">{{ selectedItems }}</span>
      <span class="caret"></span>
    </button>
    <ul class="dropdown-menu">
      <template v-if="options.length">
        <li v-if="search" class="bs-searchbox">
          <input type="text" placeholder="Search" v-model="searchText" class="form-control" autocomplete="off">
        </li>
        <li v-for="option in filteredOptions" :id="option.value" style="position:relative">
          <a @mousedown.prevent="select(option.value)" style="cursor:pointer">
      {{ option.label }}
      <span :class="[iconset, iconset + '-ok']" class="check-mark" v-show="isSelected(option.value)"></span>
      </a>
        </li>
      </template>
      <slot v-else></slot>
      <transition name="fadein">
        <div class="notify" v-show="showNotify">
          Limit reached ({{limit}} items max).
        </div>
      </transition>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      options: {
        type: Array,
        default () {
          return [];
        },
      },
      value: [String, Array],
      placeholder: {
        type: String,
        default: 'Nothing Selected',
      },
      multiple: {
        type: Boolean,
        default: false,
      },
      search: { // Allow searching (only works when options are provided)
        type: Boolean,
        default: false,
      },
      limit: {
        type: Number,
        default: 1024,
      },
      closeOnSelect: { // only works when multiple==false
        type: Boolean,
        default: false,
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      iconset: {
        type: String,
        default: 'glyphicon',
      },
    },
    mounted() {
      if (this.value.constructor !== Array) {
        if (this.value.length === 0) {
          this.value = [];
        } else {
          this.value = [this.value];
        }
      } else {
        if (!this.multiple && this.value.length > 1) {
          this.value = this.value.slice(0, 1);
        } else if (this.multiple && this.value.length > this.limit) {
          this.value = this.value.slice(0, this.limit);
        }
      }
    },
    data() {
      return {
        searchText: null,
        show: false,
        showNotify: false,
      };
    },
    computed: {
      selectedItems() {
        let foundItems = [];
        if (this.value.length) {
          for (var item of this.value) {
            if (this.options.length === 0) {
              //
              foundItems = this.value;
            } else {
              if (typeof item === "string") {
                let option;
                this.options.some(o => {
                  if (o.value === item) {
                    option = o;
                    return true;
                  }
                })
                option && foundItems.push(option.label);
              }
            }
          }
          return foundItems.join(', ');
        }
      },
      showPlaceholder() {
        return this.value.length === 0;
      },
      filteredOptions() {
        return this.options.filter(o => o.value.indexOf(this.searchText) > -1);
      },
    },
    watch: {
      value(val) {
        if (val.length > this.limit) {
          this.showNotify = true;
          this.value.pop();
          setTimeout(() => this.showNotify = false, 1000);
        }
        this.$emit('input', this.value);
      },
    },
    methods: {
      select(v) {
        if (this.value.indexOf(v) === -1) {
          if (this.multiple) {
            this.value.push(v);
          } else {
            this.value = [v];
          }
        } else {
          if (this.multiple) {
            const index = this.value.indexOf(v);
            this.value.splice(index, 1);
          }
        }
        if (this.closeOnSelect) {
          this.toggleDropdown();
        }
      },
      isSelected(v) {
        if (this.value.constructor !== Array) {
          return this.value == v;
        } else {
          return this.value.indexOf(v) !== -1;
        }
      },
      toggleDropdown() {
        this.show = !this.show;
      },
    },
  }
</script>

<style scoped>
  .bs-searchbox {
    padding: 4px 8px;
  }

  .btn-group .dropdown-menu .notify {
    position: absolute;
    bottom: 5px;
    width: 96%;
    margin: 0 2%;
    min-height: 26px;
    padding: 3px 5px;
    background: #f5f5f5;
    border: 1px solid #e3e3e3;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .05);
    pointer-events: none;
    opacity: .9;
  }
</style>



